"use client"

import { useState, useRef, useEffect } from 'react'
import { Sidebar } from './sidebar'
import { ChatArea } from './chat-area'
import { InputArea } from './input-area'
import { useTheme } from './theme-provider'
import { Button } from './ui/button'

interface Message {
  id: string
  content: string
  role: 'user' | 'assistant'
  timestamp: Date
}

interface Conversation {
  id: string
  title: string
  messages: Message[]
  lastUpdated: Date
}

export function ChatInterface() {
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [currentConversationId, setCurrentConversationId] = useState<string | null>(null)
  const [messages, setMessages] = useState<Message[]>([])
  const [isTyping, setIsTyping] = useState(false)
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [sidebarCollapsed, setSidebarCollapsed] = useState(false)
  const { theme, setTheme } = useTheme()

  useEffect(() => {
    // Load conversations from localStorage
    const savedConversations = localStorage.getItem('chat-conversations')
    if (savedConversations) {
      const parsed = JSON.parse(savedConversations)
      setConversations(parsed)
      if (parsed.length > 0 && !currentConversationId) {
        setCurrentConversationId(parsed[0].id)
        setMessages(parsed[0].messages)
      }
    }
  }, [])

  useEffect(() => {
    // Save conversations to localStorage
    if (conversations.length > 0) {
      localStorage.setItem('chat-conversations', JSON.stringify(conversations))
    }
  }, [conversations])

  const createNewConversation = () => {
    const newConversation: Conversation = {
      id: Date.now().toString(),
      title: 'New Chat',
      messages: [],
      lastUpdated: new Date(),
    }
    setConversations(prev => [newConversation, ...prev])
    setCurrentConversationId(newConversation.id)
    setMessages([])
  }

  const selectConversation = (conversationId: string) => {
    const conversation = conversations.find(c => c.id === conversationId)
    if (conversation) {
      setCurrentConversationId(conversationId)
      setMessages(conversation.messages)
    }
  }

  const sendMessage = async (content: string) => {
    if (!content.trim()) return

    const userMessage: Message = {
      id: Date.now().toString(),
      content,
      role: 'user',
      timestamp: new Date(),
    }

    // Add user message
    const newMessages = [...messages, userMessage]
    setMessages(newMessages)

    // Update conversation
    updateConversation(newMessages)

    // Show typing indicator
    setIsTyping(true)

    // Simulate AI response
    setTimeout(() => {
      const aiMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: generateAIResponse(content),
        role: 'assistant',
        timestamp: new Date(),
      }

      const finalMessages = [...newMessages, aiMessage]
      setMessages(finalMessages)
      updateConversation(finalMessages)
      setIsTyping(false)
    }, 1000 + Math.random() * 2000)
  }

  const updateConversation = (newMessages: Message[]) => {
    if (!currentConversationId) return

    setConversations(prev => prev.map(conv => {
      if (conv.id === currentConversationId) {
        const title = newMessages.length > 0 ? 
          newMessages[0].content.slice(0, 30) + (newMessages[0].content.length > 30 ? '...' : '') :
          'New Chat'
        
        return {
          ...conv,
          title,
          messages: newMessages,
          lastUpdated: new Date(),
        }
      }
      return conv
    }))
  }

  const generateAIResponse = (userMessage: string): string => {
    const responses = [
      "I understand your question. Let me help you with that.",
      "That's an interesting point. Here's what I think about it...",
      "I'd be happy to assist you with this topic.",
      "Based on what you've asked, I can provide some insights.",
      "Let me break this down for you step by step.",
      "That's a great question! Here's my perspective on it.",
    ]
    
    return responses[Math.floor(Math.random() * responses.length)] + 
           " This is a simulated response for demonstration purposes. In a real implementation, this would connect to an actual AI service.";
  }

  const toggleTheme = () => {
    setTheme(theme === 'light' ? 'dark' : 'light')
  }

  const toggleSidebarCollapse = () => {
    setSidebarCollapsed(!sidebarCollapsed)
  }

  return (
    <div className="flex h-screen bg-white dark:bg-gray-900 transition-colors">
      {/* Mobile sidebar overlay */}
      {sidebarOpen && (
        <div 
          className="fixed inset-0 bg-black bg-opacity-50 z-40 lg:hidden"
          onClick={() => setSidebarOpen(false)}
        />
      )}

      {/* Sidebar */}
      <div className={`
        fixed lg:relative inset-y-0 left-0 z-50 transform transition-transform duration-300 ease-in-out
        ${sidebarOpen ? 'translate-x-0' : '-translate-x-full lg:translate-x-0'}
      `}>
        <Sidebar
          conversations={conversations}
          currentConversationId={currentConversationId}
          onNewChat={createNewConversation}
          onSelectConversation={selectConversation}
          onToggleTheme={toggleTheme}
          theme={theme}
          isCollapsed={sidebarCollapsed}
          onToggleCollapse={toggleSidebarCollapse}
        />
      </div>

      {/* Main content */}
      <div className="flex-1 flex flex-col min-w-0">
        {/* Header */}
        <div className="flex items-center justify-between p-4 border-b border-gray-200 dark:border-gray-700 lg:hidden">
          <Button
            variant="ghost"
            size="sm"
            onClick={() => setSidebarOpen(true)}
            className="text-gray-600 dark:text-gray-300"
          >
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
            </svg>
          </Button>
          <h1 className="text-lg font-semibold text-gray-900 dark:text-white">ChatGPT Clone</h1>
          <Button
            variant="ghost"
            size="sm"
            onClick={toggleTheme}
            className="text-gray-600 dark:text-gray-300"
          >
            {theme === 'light' ? 'üåô' : '‚òÄÔ∏è'}
          </Button>
        </div>

        {/* Desktop collapse button */}
        <div className="hidden lg:flex items-center justify-between p-2 border-b border-gray-200 dark:border-gray-700">
          <Button
            variant="ghost"
            size="sm"
            onClick={toggleSidebarCollapse}
            className="text-gray-600 dark:text-gray-300"
          >
            <svg 
              className={`w-4 h-4 transition-transform ${sidebarCollapsed ? 'rotate-180' : ''}`} 
              fill="none" 
              stroke="currentColor" 
              viewBox="0 0 24 24"
            >
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 19l-7-7 7-7m8 14l-7-7 7-7" />
            </svg>
          </Button>
        </div>

        {/* Chat area */}
        <div className="flex-1 flex flex-col min-h-0">
          <ChatArea messages={messages} isTyping={isTyping} />
          <InputArea onSendMessage={sendMessage} disabled={isTyping} />
        </div>
      </div>
    </div>
  )
}
